import { FC } from 'react';
import './Tags.css';

const tags: any = [
  '   ',
  '  ',
  '  ',
  '  '
  ]

const Tags:FC = () => {
  return (
    <div className='tags__container' >
      {(tags.length !== 0) ? (
        tags?.map((tagText: any) => {
          return (
            <div className='tags__message'>
              <p className='tags__text'>{tagText}</p>
            </div>
          )
        })
      ) : (
        null
      )}
      <button className='tags__button'></button>
    </div>
  );
}

export default Tags;
